@media only screen and (max-width: 767px) {
  #contact-panel:before {
    content: '';
    background-color: #2c3338;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  #contact-panel:after {
    content: '';
    background-color: #2c3338;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 5rem;
    width: 100%;
    z-index: 100;
    display: block;
  }
  #contact-panel {
    margin: 5rem 0;
  }
  #contactImage {
    padding: 5rem 0 5rem 0;
    position: fixed;
    width: auto;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  .contact-wrapper {
    min-width: 100%;
    min-height: 100vh;
    background-color: rgba(10, 10, 10, 0.7);
    margin: 7rem auto 7rem auto;
    padding: 20px;
    border-radius: 4px;
    box-shadow: 0 0 10px black;
  }
  .contact-title {
    margin-top: 30px;
    font-family: "Sansita", sans-serif;
    text-align: center;
    font-size: 4.2rem;
    font-weight: 600;
    color: #8eadB1;
  }
  .contact-input-container {
    display: block;
  }
  .contact-input-container > .contact-input {
    max-width: 100%;
  }
  .contact-label-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    width: 100%;
    height: 50px;
  }
  .contact-label {
    font-family: "Sansita", sans-serif;
    font-font-weight: 700;
    font-size: 2.4rem;
    color: #e4cd8a;
    margin: 10px 20px;
  }
  .contact-input {
    width: 100%;
    font-size: 2.4rem;
    font-family: "Roboto Slab", serif;
    margin: 20px 0px 0px 0px;
    border-radius: 4px;
    position: relative;
  }
  .ui.form.contact-input > textarea#description {
    width: 100%;
    resize: none;
  }
  .ui.input input, .ui.form textarea {
    transition: all 0.3s linear;
    font-family: "Sansita", sans-serif;
    font-size: 1.8rem;
  }
  .ui.input input.contact-input-error, .ui.form textarea.contact-input-error {
    border: 1px #f4424e solid;
    background-color: rgba(244, 66, 78, 0.1);
    color: #dcddde;
  }
  .contact-list-error {
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
    background-color: rgba(244, 66, 78, 0.1);
    color: #f4424e;
  }
  .contact-list-error > li {
    list-style: none;
    padding: 5px 0;
    margin: 10px 20px;
    line-height: 2.4rem;
  }
  #form-control > i.icon {
    tranistion: all 0.3s linear;
  }
  .icon-error {
    color: #dcddde;
  }
  .contact-button {
    font-family: "Sansita", sans-serif;
    font-size: 2.6rem;
    font-weight: 600;
    width: 100%;
    margin: 20px 0;
    padding: 1.5rem;
    text-align: center;
    background-color: #638a8f;
    color: #e4cd8a;
    border: none;
    border-radius: 4px;
    transition: all 0.3s linear;
  }
  .contact-button:hover {
    background-color: #445f62;
  }
}

@media only screen and (orientation: landscape) and (max-width: 767px) {
  #contactImage {
    width: 100vw;
    height: auto;
  }
  #contact-panel:after {
    content: '';
    display: none;
  }
  .contact-wrapper {
    margin: 8rem auto 1rem auto;
  }
}

