@media only screen and (max-width: 767px) {
  #login-panel:before {
    content: '';
    background-color: #2c3338;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  #login-panel:after {
    content: '';
    background-color: #2c3338;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 5rem;
    width: 100%;
    z-index: 100;
    display: block;
  }
  #loginImage {
    position: fixed;
    width: auto;
    padding: 5rem 0 5rem 0;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  .login-wrapper {
    width: 100%;
    min-height: 100vh;
    background-color: rgba(5, 5, 5, 0.7);
    margin: 7rem auto 7rem auto;
    padding: 20px;
    border-radius: 4px;
    box-shadow: 0 0 10px black;
  }
  .login-title {
    font-family: "Sansita", sans-serif;
    text-align: center;
    font-size: 4.2rem;
    font-weight: 600;
    padding-bottom: 30px;
    color: #8eadB1;
  }
  .login-label-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
    width: 100%;
    height: 50px;
  }
  .login-label {
    font-family: "Sansita", sans-serif;
    font-font-weight: 700;
    font-size: 2.4rem;
    color: #e4cd8a;
    margin: 10px 20px;
  }
  .login-input {
    width: 100%;
    font-size: 2.4rem;
    font-family: "Roboto Slab", serif;
    margin: 20px 0px;
    border: none;
    border-radius: 4px;
  }
  #login-control > .ui.input input {
    font-family: "Sansita", sans-serif;
    transition: all 0.3s linear;
    font-size: 2.4rem;
  }
  .ui.input input.login-input-error {
    border: 1px #f4424e solid;
    background-color: rgba(244, 66, 78, 0.1);
    color: #dcddde;
  }
  #login-control > i.icon {
    tranistion: all 0.3s linear;
  }
  .login-list-error {
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
    background-color: rgba(244, 66, 78, 0.1);
    color: #f4424e;
    margin: 20px;
  }
  .login-list-error > li {
    list-style: none;
    padding: 5px 0;
    margin: 10px 20px;
    line-height: 2.4rem;
  }
  .login-button {
    font-family: "Sansita", sans-serif;
    font-size: 2.6rem;
    font-weight: 600;
    width: 100%;
    padding: 1.5rem;
    margin: 20px 0px;
    background-color: #638a8f;
    color: #e4cd8a;
    border: none;
    border-radius: 4px;
    transition: all 0.3s linear;
  }
  .login-button:hover {
    background-color: #445f62;
  }
}

@media only screen and (orientation: landscape) and (max-width: 767px) {
  #loginImage {
    width: 100vw;
    height: auto;
  }
  #login-panel:after {
    content: '';
    display: none;
  }
  .login-wrapper {
    margin: 8rem auto 1rem auto;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1279px) {
