    content: '';
    background-color: #000000;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  #login-panel {
    width: 100%;
    height: 100%;
  }
  #loginImage {
    position: fixed;
    width: auto;
    height: 100vh;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  .login-wrapper {
    width: 540px;
    min-height: 540px;
    background-color: rgba(10, 10, 10, 0.7);
    margin: 12rem auto 6rem auto;
    padding: 40px;
    border-radius: 4px;
    box-shadow: 0 0 10px black;
  }
  .login-title {
    font-family: "Sansita", sans-serif;
    text-align: center;
    font-size: 3.6rem;
    font-weight: 600;
    color: #8eadB1;
  }
  .login-input-container {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .login-input-container > .login-input {
    width: 100%;
  }
  .login-label-container {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    width: 100%;
  }
  .login-label {
    font-family: "Sansita", sans-serif;
    font-font-weight: 700;
