    color: #e4cd8a;
    margin: 10px 20px;
  }
  .login-input {
    width: 100%;
    font-size: 1.6rem;
    font-family: "Roboto Slab", serif;
    margin: 20px 0px;
    border: none;
    border-radius: 4px;
  }
  .ui.input input {
    transition: all 0.3s linear;
    font-family: "Sansita", sans-serif;
    font-size: 1.6rem;
  }
  .ui.input input.login-input-error {
    border: 1px #f4424e solid;
    background-color: rgba(244, 66, 78, 0.1);
    color: #dcddde;
  }
  .login-list-error {
    font-family: "Roboto Slab", serif;
    font-size: 1.6rem;
    background-color: rgba(244, 66, 78, 0.1);
    color: #f4424e;
    margin: 0 20px;
  }
  .login-list-error > li {
    list-style: none;
    padding: 5px 0;
    margin: 10px 20px;
    line-height: 1.4rem;
  }
  #form-control > i.icon {
    tranistion: all 0.3s linear;
  }
  .icon-error {
    color: #dcddde;
  }
  .login-button {
    font-family: "Sansita", sans-serif;
    font-size: 2rem;
    font-weight: 600;
    width: 100%;
    padding: 10px;
    margin: 20px 0px;
    background-color: #638a8f;
    color: #e4cd8a;
    border: none;
    border-radius: 4px;
    transition: all 0.3s linear;
  }
  .login-button:hover {
    background-color: #445f62;
  }
}

@media only screen and (orientation: portrait) and (min-width: 768px) and (max-width: 1279px) {
  #contactImage {
    width: 100%;
    height: auto;
  }
  .contact-wrapper {
    margin: 12rem auto 32rem auto;
  }
}

