@media only screen and (max-width: 767px) {
  .about-panel {
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-around;
  }
  .about-panel:after {
    content: '';
    background-color: #2c3338;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 5rem;
    width: 100%;
    z-index: 100;
    display: block;
  }
  .about-panel {
    margin: 5rem 0;
  }
  #aboutImage {
    position: fixed;
    width: auto;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  .about-heading {
    font-family: "Amatic SC", cursive;
    font-size: 4.2rem;
    color: #e4cd8a;
    text-align: center;
  }
  .about-gallery {
    width: 100%;
    max-height: 100%;
    margin-top: 5%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    overflow: hidden;
    margin-bottom: 5rem;
  }
  .about-gallery .photo {
    margin: 10px 15px;
    min-width: 100px;
    min-height: 170px;
    background: #bfb6ad;
    padding: 15px;
    border-radius: 5px;
    border: 1px solid #4e4945;
    transform: rotate(4deg);
    transition: all 0.3s linear;
  }
  .about-gallery .photo > img {
    display: block;
    margin-left: 0;
    margin-right: 0;
    width: auto;
    height: 100px;
    max-height: 200px;
    flex-basis: 100%;
  }
  .about-gallery .photo > figcaption {
    font-family: "Sansita", sans-serif;
    font-size: 2rem;
    margin-top: 10px;
    text-align: center;
    color: #4e4945;
    transition: all 0.3s linear;
  }
  .about-gallery .photo:nth-child(even) {
    transform: rotate(-2deg) scale(0.9);
  }
  .about-gallery .photo:hover {
    transition: all 0.3s linear;
    transform: scale(1.1);
    border: 1px solid #000000;
    background: #e4ddce;
  }
  .about-gallery .photo:hover > figcaption {
    color: #000000;
  }
  .about-letter {
    margin-top: 4rem;
    width: 100%;
  }
  .letter {
    background: #e4ddce;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    margin: 2rem auto;
    width: 100%;
    height: 100%;
    padding: 1.5rem;
    position: relative;
    width: 80%;
  }
  .letter:before, .letter:after {
    content: '';
    height: 98%;
    position: absolute;
    width: 85%;
    z-index: -1;
  }
  .letter:before {
    background: #d2c6ad;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
    left: -1;
    top: 4px;
    transform: rotate(-2deg);
  }
  .letter:after {
    background: #c0af8c;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
    right: -5px;
    top: 1px;
    transform: rotate(1deg);
  }
  .letter > p, .letter > ul > li {
    font-family: "Courgette", cursive;
    font-size: 2rem;
    line-height: 2.4rem;
    color: #000000;
  }
  .letter > ul > li {
    list-style-type: none;
    line-height: 2.4rem;
  }
  .photo-portfolio {
    position: block;
    width: 50%;
  }
  .photo-portfolio .photo {
    margin: 10px 20px;
    min-width: 150px;
    min-height: 170px;
    background: #bfb6ad;
    padding: 10px;
    transition: all 0.3s linear;
    transform: rotate(4deg);
  }
  .photo-portfolio .photo:after {
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.5);
  }
  .photo-portfolio .photo > img {
    width: 200px;
    height: auto;
    float: right;
  }
  .photo-portfolio .photo figcaption {
    font-family: "Sansita", sans-serif;
    font-size: 2rem;
    text-align: center;
    margi-top: 10px;
    color: #4e4945;
    transition: all 0.3s linear;
  }
  .photo-portfolio .photo:hover {
    transition: all 0.3s linear;
    transform: scale(1.1);
  }
  .photo-portfolio .photo:hover > figcaption {
    color: #000000;
  }
}

@media only screen and (orientation: landscape) and (max-width: 767px) {
  #aboutImage {
    width: 100%;
    height: auto;
  }
  .about-panel:after {
    content: '';
    display: none;
  }
  .letter {
    padding: 3rem;
  }
  .letter:before, .letter:after {
    content: '';
    height: 98%;
    position: absolute;
    width: 85%;
    z-index: -1;
  }
  .letter:before {
    background: #d2c6ad;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
    left: -1;
    top: 4px;
    transform: rotate(-4deg);
  }
  .letter:after {
    background: #c0af8c;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
    right: -5px;
    top: 1px;
    transform: rotate(2deg);
  }
}

