@media only screen and (max-width: 767px) {
  .blog-panel:before {
    content: '';
    background-color: #2c3338;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  .blog-panel:after {
    content: '';
    background-color: #2c3338;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 5rem;
    width: 100%;
    z-index: 100;
    display: block;
  }
  .blog-panel {
    margin: 5rem 0;
  }
  #blogImage {
    background-color: #2c3338;
    padding: 5rem 0 5rem 0;
    position: fixed;
    width: auto;
    height: 100%;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  .blog-wrapper {
    width: 100%;
    height: 100%;
    margin: 7rem auto 7rem auto;
    padding: 20px;
    border-radius: 4px;
  }
  .blog-heading {
    font-family: "Playfair Display", serif;
    font-size: 3.6rem;
    text-align: center;
    margin: 10rem auto 0 auto;
    color: #8eadB1;
  }
  .blog-title {
    font-family: "Playfair Display", serif;
    font-size: 2.6rem;
    font-weight: 700;
    line-height: 3rem;
    min-height: 4rem;
    color: #2c3338;
    margin: 60px 0 0 0;
    padding: 1rem 3rem;
    background: #e4cd8a;
    border-radius: 2rem 2rem 0 0;
  }
  .blog-segment {
    font-family: "Roboto Slab", serif;
    font-size: 2.4rem;
    line-height: 2.6rem;
    color: #2c3338;
    top: 0;
    bottom: 0;
    background: #e4ddce;
    border-radius: 0 0 2rem 2rem;
    position: relative;
    margin: 0 -1px;
    padding: 3rem 3rem 5rem 3rem;
    width: calc(100% + 2px);
    max-width: calc(100% + 2px);
    box-shadow: none;
    border: 1px solid #D4D4D5;
  }
  .blog-paragraph {
    margin-bottom: 3rem;
  }
  .blog-button {
    font-family: "Sansita", sans-serif;
    font-size: 3rem;
    font-weight: 600;
    display: inline-block;
    cursor: pointer;
    width: 100%;
    padding: 1.5rem;
    text-align: center;
    background-color: #638a8f;
    color: #e4cd8a;
    border: none;
    border-radius: 4px;
    transition: all 0.3s linear;
  }
  .blog-button:hover {
    background-color: #445f62;
    color: #e4cd8a;
  }
  .blog-pagination-wrapper {
    margin: 0 auto 10rem auto;
  }
  .blog-pagination {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .blog-number {
    font-family: "Playfair Display", serif;
    font-size: 3rem;
    font-weight: 600;
    line-height: 3rem;
    margin: 1.5rem;
    color: #e4cd8a;
    background: #2c3338;
    padding: 2rem;
    border-radius: 4px;
  }
  .blog-number:hover {
    color: #e4ddce;
    background: #597c80;
    padding: 2rem;
    border-radius: 4px;
    transition: all 0.3s linear;
  }
  .blog-number.active {
    color: #e4cd8a;
    background: #597c80;
  }
  .blog-number.active:hover {
    color: #e4cd8a;
    background: #597c80;
  }
}

@media only screen and (orientation: landscape) and (max-width: 767px) {
  #blogImage {
    width: 100vw;
    height: auto;
    background-color: #2c3338;
  }
  .blog-panel:after {
    content: '';
    display: none;
  }
  .blog-wrapper {
    margin: 8rem auto 1rem auto;
  }
  .blog-pagination-wrapper {
    margin: 3rem auto;
  }
}

